.AwardView {
    width: 100%;
    min-height: 110vh;
    padding-left: 10%;
    padding-right: 10%;
    background: transparent;
}
.award-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    align-items:center;
    justify-content: center;
    padding-bottom: 100px;
}


@media screen and (max-width: 960px) {
    .AwardView {
        padding-left: 5%;
        padding-right: 5%;
    }
}